"use client";

import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormMessage,
} from "@/components/ui/form";
import { useForm } from "react-hook-form";
import { Input } from "@/components/ui/input";
import { Plus, Search } from "lucide-react";
import { ContrAgentModal } from "@/components/modal";
import { useState, useEffect } from "react";

const ContractorsPage = () => {
  const [isOpen, setIsOpen] = useState(false);
  const toggleOpen = () => setIsOpen((prev) => !prev);

  const {
    control,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const onSubmit = (data: any) => {
    console.log(data);
  };

  // 1. On mount, check for the hash
  useEffect(() => {
    if (typeof window !== "undefined" && window.location.hash === "#add_contractor") {
      setIsOpen(true);
    }
  }, []);

  // 2. (Optional) Keep the hash in sync when opening/closing
  useEffect(() => {
    if (typeof window === "undefined") return;

    if (isOpen) {
      // push the hash without reloading
      window.history.replaceState(null, "", "#add_contractor");
    } else {
      // remove the hash
      window.history.replaceState(null, "", window.location.pathname + window.location.search);
    }
  }, [isOpen]);

  return (
    <div className="pt-5 pb-10">
      <form
        noValidate
        onSubmit={handleSubmit(onSubmit)}
        className="pb-5 flex flex-col gap-4 w-[30%]"
      >
        <FormField
          control={control}
          name="search"
          render={({ field }) => (
            <FormItem>
              <FormControl>
                <div className="relative w-full">
                  <Input
                    {...field}
                    type="text"
                    placeholder="Заказ, серийный номер, название"
                    className="pr-10 text-[14px] leading-[25px] h-[41px]"
                  />
                  <Search
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-darkSoul"
                    size={20}
                  />
                </div>
              </FormControl>
              <FormMessage>{errors.search?.message}</FormMessage>
            </FormItem>
          )}
        />

        <FormField
          control={control}
          name="select"
          render={({ field }) => (
            <FormItem>
              <FormControl>
                <label
                  htmlFor="file-upload"
                  onDragOver={(e) => e.preventDefault()}
                  className="flex flex-col gap-4 items-center justify-center border-dashed border-2 border-[#E3E2E2] w-full h-[205px] cursor-pointer hover:bg-gray-50 transition relative"
                >
                  <input
                    id="file-upload"
                    type="file"
                    {...field}
                    className="hidden"
                    onChange={handleFileChange}
                  />

                  <>
                    <Plus className="text-darkSoul w-10 h-10" />
                    <p className="text-darkSoul mt-2">Добавить контрагенты</p>
                  </>
                </label>
              </FormControl>
            </FormItem>
          )}
        />
      </form>

      <ContrAgentModal isOpen={isOpen} toggleOpen={toggleOpen} />
    </div>
  );
};

export default ContractorsPage;
